import { useRef, useState } from 'react';
import { Icon } from '@iconify/react';
// material
import { alpha } from '@mui/material/styles';
import { Box, MenuItem, ListItemIcon, ListItemText } from '@mui/material';
// components
import MenuPopover from '../../components/MenuPopover';
import { MIconButton } from '../../components/@material-extend';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
// ----------------------------------------------------------------------

const LANGS = [
  {
    value: 'en',
    label: 'Add Enquiry',
    icon: '/static/icons/navbar/ic_analytics.svg'
  },
  {
    value: 're',
    label: 'Add Repair',
    icon: '/static/icons/navbar/ic_analytics.svg'
  },
  {
    value: 'or',
    label: 'Add Order',
    icon: '/static/icons/navbar/ic_analytics.svg'
  }
];

// ----------------------------------------------------------------------

export default function LanguagePopover() {
  const anchorRef = useRef(null);
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <MIconButton
        ref={anchorRef}
        onClick={handleOpen}
        sx={{
          padding: 0,
          width: 36,
          height: 36,
          ...(open && {
            bgcolor: (theme) => alpha(theme.palette.primary.main, theme.palette.action.focusOpacity)
          })
        }}
      >
        <Fab
          style={{
            backgroundColor: '#B98900',
            boxShadow: '0 7px 14px rgba(145, 106, 0, 0.6), 0 5px 5px rgba(185, 137, 0, 1)',
            width: '32px',
            height: '32px'
          }}
        >
          <AddIcon sx={{ color: '#F6F6F7', width: '11px', height: '14px' }} />
        </Fab>
      </MIconButton>

      <MenuPopover open={open} onClose={handleClose} anchorEl={anchorRef.current}>
        <Box sx={{ py: 1 }}>
          <MenuItem onClick={handleClose} sx={{ py: 1, px: 2.5 }}>
            <ListItemIcon>
              <Icon
                icon="mdi:salesforce"
                style={{
                  padding: 3,
                  borderRadius: '50%',
                  border: '1px solid #919EAB32',
                  fontSize: '24px',
                  color: '#db909a'
                }}
              />
            </ListItemIcon>
            <ListItemText primaryTypographyProps={{ variant: 'body2' }}>Add Enquiry</ListItemText>
          </MenuItem>
        </Box>
        <Box sx={{ py: 1 }}>
          <MenuItem onClick={handleClose} sx={{ py: 1, px: 2.5 }}>
            <ListItemIcon>
              <Icon
                icon="fluent:wrench-20-regular"
                style={{
                  padding: 3,
                  borderRadius: '50%',
                  border: '1px solid #919EAB32',
                  fontSize: '24px',
                  color: '#6c98f1'
                }}
              />
            </ListItemIcon>
            <ListItemText primaryTypographyProps={{ variant: 'body2' }}>Add Repair</ListItemText>
          </MenuItem>
        </Box>
        <Box sx={{ py: 1 }}>
          <MenuItem onClick={handleClose} sx={{ py: 1, px: 2.5 }}>
            <ListItemIcon>
              <Icon
                icon="mdi:currency-usd"
                style={{
                  padding: 3,
                  borderRadius: '50%',
                  border: '1px solid #919EAB32',
                  fontSize: '24px',
                  color: '#b72136'
                }}
              />
            </ListItemIcon>
            <ListItemText primaryTypographyProps={{ variant: 'body2' }}>Add Order</ListItemText>
          </MenuItem>
        </Box>
        {/* <Box sx={{ py: 1 }}>
          {LANGS.map((option) => (
            <MenuItem
              key={option.value}
              selected={option.value === LANGS[0].value}
              onClick={handleClose}
              sx={{ py: 1, px: 2.5 }}
            >
              <ListItemIcon>
                <Icon icon="mdi:salesforce" color="#db909a" />
                <Icon icon="fluent:wrench-20-regular" color="#6c98f1" />
                <Icon icon="mdi:currency-usd" color="#b72136" />
              </ListItemIcon>
              <ListItemText primaryTypographyProps={{ variant: 'body2' }}>{option.label}</ListItemText>
            </MenuItem>
          ))}
        </Box> */}
      </MenuPopover>
    </>
  );
}
