import { useRef, useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
// routes
import { PATH_AUTH, PATH_DASHBOARD, PATH_Gate } from '../../routes/paths';
import { Icon } from '@iconify/react';
import GridViewOutlinedIcon from '@mui/icons-material/GridViewOutlined';
// material
import { alpha } from '@mui/material/styles';
import { Box, Stack, Typography } from '@mui/material';
import MenuPopover from '../../components/MenuPopover';
import { MIconButton } from '../../components/@material-extend';
import { makeStyles } from '@mui/styles';

// ----------------------------------------------------------------------
const useStyles = makeStyles({
  appicon: {
    width: '30%',
    alignItems: 'center',
    marginLeft: 0,
    fontSize: '10px'
  },
  appbox: {
    justifyContent: 'space-between'
  },
  appfont: {
    fontSize: '10px'
  },
  appimg: {
    width: 20,
    height: 20
  }
});

export default function AnotherPopover() {
  const anchorRef = useRef(null);
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const mstyle = useStyles();

  return (
    <>
      <MIconButton
        ref={anchorRef}
        size="small"
        color={open ? 'primary' : 'default'}
        onClick={handleOpen}
        sx={{
          ...(open && {
            bgcolor: (theme) => alpha(theme.palette.primary.main, theme.palette.action.focusOpacity)
          })
        }}
      >
        <GridViewOutlinedIcon />
      </MIconButton>

      <MenuPopover open={open} onClose={handleClose} anchorEl={anchorRef.current} sx={{ width: 200 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', py: 2, px: 2.5 }}>
          <Box sx={{ flexGrow: 1 }}>
            <Typography variant="subtitle2" sx={{ fontWeight: 'bold', mb: '20px' }}>
              App
            </Typography>
            <Stack direction="row" spacing={3} className={mstyle.appbox}>
              <Stack className={mstyle.appicon}>
                <RouterLink underline="none" component={RouterLink} to={PATH_DASHBOARD.general.analytics}>
                  <img src="/image/sales.png" className={mstyle.appimg} />
                  <Typography className={mstyle.appfont}>Sales</Typography>
                </RouterLink>
              </Stack>
              <Stack className={mstyle.appicon}>
                <RouterLink underline="none" component={RouterLink} to={PATH_Gate.general.gatedashboard}>
                  <img src="/image/gate.png" className={mstyle.appimg} />
                  <Typography variant="subtitle2" className={mstyle.appfont}>
                    Gate
                  </Typography>
                </RouterLink>
              </Stack>
              <Stack className={mstyle.appicon}>
                <img src="/image/setting.png" className={mstyle.appimg} />
                <Typography variant="subtitle2" className={mstyle.appfont}>
                  Setting
                </Typography>
              </Stack>
            </Stack>
          </Box>
        </Box>
      </MenuPopover>
    </>
  );
}
