import { Suspense, lazy } from 'react';
import { Navigate, useRoutes, useLocation } from 'react-router-dom';
// layouts
import MainLayout from '../layouts/main';
import DashboardLayout from '../layouts/dashboard';
import LogoOnlyLayout from '../layouts/LogoOnlyLayout';
// components
import LoadingScreen from '../components/LoadingScreen';
// guards
import GuestGuard from '../guards/GuestGuard';
import AuthGuard from '../guards/AuthGuard';
// ----------------------------------------------------------------------

const Loadable = (Component) => (props) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const { pathname } = useLocation();
  const isDashboard = pathname.includes('/dashboard');

  return (
    <Suspense
      fallback={
        <LoadingScreen
          sx={{
            ...(!isDashboard && {
              top: 0,
              left: 0,
              width: 1,
              zIndex: 9999,
              position: 'fixed'
            })
          }}
        />
      }
    >
      <Component {...props} />
    </Suspense>
  );
};

export default function Router() {
  return useRoutes([
    {
      // Authentication Routes
      path: 'auth',
      children: [
        {
          path: 'login',
          element: (
            <GuestGuard>
              <Login />
            </GuestGuard>
          )
        },
        {
          path: 'register',
          element: (
            <GuestGuard>
              <Register />
            </GuestGuard>
          )
        },
        { path: 'login-unprotected', element: <Login /> },
        { path: 'register-unprotected', element: <Register /> },
        { path: 'reset-password', element: <ResetPassword /> },
        { path: 'verify', element: <VerifyCode /> }
      ]
    },

    // Admin Routes
    {
      path: 'admin',
      element: (
        <GuestGuard>
          <DashboardLayout />
        </GuestGuard>
      ),
      children: [
        { element: <Navigate to="/admin/roles" replace /> },
        { path: 'roles', element: <Roles /> },
        { path: 'addemployee', element: <AddEmployee /> }
      ]
    },

    // Dashboard Routes
    {
      path: 'dashboard',
      element: (
        <GuestGuard>
          <DashboardLayout />
        </GuestGuard>
      ),
      children: [
        { element: <Navigate to="/dashboard/analytics" replace /> },
        { path: 'analytics', element: <Analytics /> },
        { path: 'enquiries', element: <Enquiries /> },
        { path: 'profile', element: <Profile /> },
        { path: 'tasks', element: <Tasks /> },
        { path: '/dashboard/enquiries/singleenquiry', element: <SingleEnquiry /> }
      ]
    },

    //GateApp Routes
    {
      path: 'gate',
      element: (
        <GuestGuard>
          <DashboardLayout />
        </GuestGuard>
      ),
      children: [
        { element: <Navigate to="/gate/dashboard" replace /> },
        { path: 'dashboard', element: <GateDashboard /> },
        { path: 'directory', element: <Directory /> },
        { path: 'checkout', element: <CheckOut /> },
        { path: 'addvisitor', element: <AddVisitor /> },
        { path: 'gatefullprofile', element: <GateFullProfile /> }
      ]
    },

    // Main Routes
    {
      path: '*',
      element: <LogoOnlyLayout />,
      children: [
        { path: '404', element: <NotFound /> },
        { path: '*', element: <Navigate to="/404" replace /> }
      ]
    },
    {
      path: '/',
      element: <Navigate to="/auth/login" replace />
    },
    { path: '*', element: <Navigate to="/404" replace /> }
  ]);
}

// IMPORT COMPONENTS

// Authentication
const Login = Loadable(lazy(() => import('../pages/authentication/Login')));
const Register = Loadable(lazy(() => import('../pages/authentication/Register')));
const ResetPassword = Loadable(lazy(() => import('../pages/authentication/ResetPassword')));
const VerifyCode = Loadable(lazy(() => import('../pages/authentication/VerifyCode')));
// Admin Page
const AddEmployee = Loadable(lazy(() => import('../pages/admin/AddEmployee')));
const Roles = Loadable(lazy(() => import('../pages/admin/Roles')));
// Dashboard
const Analytics = Loadable(lazy(() => import('../pages/Analytics')));
const Enquiries = Loadable(lazy(() => import('../pages/Enquiries')));
const Profile = Loadable(lazy(() => import('../pages/Profile')));
const Tasks = Loadable(lazy(() => import('../pages/Tasks')));
const NotFound = Loadable(lazy(() => import('../pages/Page404')));
// GateApp
const GateDashboard = Loadable(lazy(() => import('../pages/gate/GateDashboard')));
const Directory = Loadable(lazy(() => import('../pages/gate/Directory')));
const CheckOut = Loadable(lazy(() => import('../pages/gate/CheckOut')));
const AddVisitor = Loadable(lazy(() => import('../pages/gate/AddVisitor')));
const GateFullProfile = Loadable(lazy(() => import('../pages/gate/GateFullProfile')));
// Main
// const LandingPage = Loadable(lazy(() => import('../pages/LandingPage')));
const SingleEnquiry = Loadable(lazy(() => import('../pages/SingleEnquiry')));
