// routes
import { PATH_Gate } from '../../routes/paths';
// components
import SvgIconStyle from '../../components/SvgIconStyle';

// ----------------------------------------------------------------------

const getIcon = (name) => (
  <SvgIconStyle src={`/static/icons/navbar/${name}.svg`} sx={{ width: '100%', height: '100%' }} />
);

const ICONS = {
  user: getIcon('ic_user'),
  ecommerce: getIcon('ic_ecommerce'),
  analytics: getIcon('ic_analytics'),
  dashboard: getIcon('ic_dashboard')
};

const gatesidebarConfig = [
  // GENERAL
  // ----------------------------------------------------------------------
  {
    subheader: 'general',
    items: [
      { title: 'Dashboard', path: PATH_Gate.general.gatedashboard, icon: ICONS.dashboard },
      { title: 'Directory', path: PATH_Gate.general.directory, icon: ICONS.analytics },
      { title: 'CheckOut', path: PATH_Gate.general.checkout, icon: ICONS.ecommerce },
      { title: 'AddVisitor', path: PATH_Gate.general.addvisitor, icon: ICONS.analytics }
      // { title: 'GateFullProfile', path: PATH_Gate.general.gatefullprofile, icon: ICONS.analytics }
    ]
  }
];

export default gatesidebarConfig;
